
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {

components: { Flexbox, FlexboxItem, Divider, XButton },

data () {
return {
is_edit: false,
is_btn: true,
}
},

computed: {},

beforeMount () {},

mounted () {
this.$refs.content_box.style.height = document.documentElement.clientHeight - 116 + 'px';
},

methods: {
goBack() {
this.$router.go(-1);
},

edit() {
this.is_edit = true;
this.is_btn = false;
},

submit() {
this.$router.go(-1);
}
},

watch: {}

}




